/*
Try and keep global SCSS and CSS to a minimum and mainly to global variables.  This prevents components from breaking when used in other projects
*/
:root {    
  --color-blue: #337ab7;
  --color-info: #5bc0de;
  --color-success: #5cb85c;
  --color-warning: #f0ad4e;
  --color-danger: #d9534f;
}
